import { cacheFunction } from '@/utils/functions';

const YANDEX_COUNTER = 69045682;

/**
 * Отправить яндекс метрику
 * @param {string} target
 */
const sendReachGoal = (target = '') => {
  window.ym && window.ym(YANDEX_COUNTER, 'reachGoal', target);
};

/**
 * Отправить яндекс метрику с одинаковой целью 1 раз
 */
export const reachGoal = cacheFunction(sendReachGoal);
