import store from '@/store';

import { queryParams } from './url';
import { isEmpty } from './functions';

export const setRequestParams = () => {
  const params = queryParams();

  if (!isEmpty(params)) {
    const items = [];
    const paramsList = Object.entries(params);

    // Формируем список товаров из полученных get параметров.
    paramsList.forEach(([key, value]) => {
      if (key.includes('obj')) {
        const [string, field] = key.split('_');
        const index = string.replace('obj', '');
        if (!items[index]) items[index] = {};

        // добавляем товары в items
        items[index][field] = params[key];

        // считаем сумму всех товаров
        const totalSum = items.reduce((sum, item) => sum + item.price * item.count, 0);

        // записываем сумму товаров в store
        if (totalSum) store.commit('setState', { name: 'totalSum', value: totalSum });
        store.commit('setState', { name: 'items', value: items });
      } else {
        store.commit('setState', { name: key, value });
      }
    });
  }

  // Добавляем временные параметры (часовой пояс, начальное время заполнения анкеты)
  const utc = new Date().getTimezoneOffset();
  const timeStart = new Date().getTime();

  store.commit('setState', { name: 'utc', value: utc });
  store.commit('setState', { name: 'time_start', value: timeStart });
};
